<template>
    <div id="aboutTz">
        <section class="wrap aboutMe">
            <template>
            <el-tabs :tab-position="tabPosition">
                <el-tab-pane label="关于我们">
                    <aboutMe></aboutMe>
                </el-tab-pane>
                <el-tab-pane label="用户协议">
                    <userPact></userPact>
                </el-tab-pane>
                <el-tab-pane label="使用帮助">
                    <useHelp></useHelp>
                </el-tab-pane>
                <el-tab-pane label="定制家具">
                    <DIY></DIY>
                </el-tab-pane>
            </el-tabs>
            </template>
        </section>
        
    </div>
</template>
<script>
import aboutMe from '@/components/about/aboutChild/about'
import userPact from '@/components/about/aboutChild/userPact'
import useHelp from '@/components/about/aboutChild/useHelp'
import DIY from '@/components/about/aboutChild/diy'
export default {
    name: 'aboutTz',
    data () {
        return {
            msg : '关于我们',
            tabPosition: 'left'
        }
    },
    components:{
        aboutMe,
        userPact,
        useHelp,
        DIY
    }
}
</script>
<style>
    .aboutMe{
        margin-top: 15px;
        margin-bottom: 33px;
    }
    .aboutMe .el-tabs__header{
        width: 223px;
        height: 185px;
        background: #fff!important;
        text-align: left!important;
        padding-top: 4px;
    }
    
    
    .el-tabs--left .el-tabs__item.is-left{
        text-align: left;
        /* border-left: 2px solid #fff!important; */
    }
    .is-active{
        border-left: 2px solid #e1a638!important;
    }
    .el-tabs__item{
        line-height: 46px;
        height: 46px;
        padding: 0 25px;
    }
    .el-tabs__item.is-active{
        color: #e1a638!important;
    }
    .el-tabs--left .el-tabs__active-bar.is-left, .el-tabs--left .el-tabs__nav-wrap.is-left::after{
        left:0;
        
    }
   .el-tabs__nav-scroll .el-tabs__item:hover{
        color: #fff!important;
        border-left: 2px solid #e1a638!important;
        background: #e1a638!important;
    }
    .el-tabs__nav-wrap::after{
        background: transparent;
    }
    .el-tabs__active-bar{
        display: none;
    }
    .el-tabs--left .el-tabs__header.is-left{
        margin-right: 15px;
    }
</style>


