                        <p>建站十年，我们积累了一些优质厂商资源。在此基础上仔细甄选合作厂商，推出家具定制服务，您在使用本服务之前，请务必阅读以下内容。
家具定制流程</p>
                    </li>
                    <li>
                        <h5>定制家具流程</h5>
                        <p class="diyProcess">发布需求  -  支付定金  -  开始制作  -  制作完成  -  支付尾款  -  物流配送  -  验收交付</p>
                        <ul class="diyMsgBox">
