                                <span>发布需求：</span>
                                <span>您在发布需求后，客服人员会在1个工作日内和您联系确认定制需求。</span>
                            </li>
                            <li>
                                <span>支付定金：</span>
                                <span>确认需求后，您需要支付30%的定金。</span>
                            </li>
                            <li>
                                <span>开始制作：</span>
                                <span>收到定金后，会立即采购所需原材料进行制作。</span>
                            </li>
                            <li>
                                <span>制作完成：</span>
                                <span>制作完成后，会先进行远程照片验收。确认无误后，支付尾款至平台，进行物流配送。</span>
                            </li>
                            <li>
                                <span>物流配送：</span>
                                <span>大件家具商品会通过物流进行配送，物流费用和送上门费用由地区决定，价格差异较大，所以暂不支持免费送货上门服务。如遇需安装类商品，厂商会提供指导或上门安装服务。</span>
                            </li>
                            <li>
                                <span>验收交付：</span>
                                <span>为保障消费者自身权益，建议消费者在签收商品前务必进行当面验收，检查货物外包装及内物外观，在确定货物完好无损后，再进行签收；如果物流公司不支持先验货再签收，请致电拓者客服之后拒绝签收。如在验收过程中发现外包装破损、商品出现破损、少件等问题，请自行拍照并让配送人员签字，保留相关证据，联系拓者客服进行核实处理；如消费者未进行验收即做签收，将默认为消费者已经对商品进行了当面验收，商品后期出现问题建议消费者联系拓者客服做售后处理
因大件家具物流成本极高，如出现零部件质量问题，优先以换件、补件为主；玻璃制品和易损制品局部损坏，可由平台直接补寄，无需全部退回。</span>
                            </li>
                        </ul>
                    </li>
                    <li>
