                        <p>根据法律规定和商品性质，定制类家具开始制作后，不支持退定金，也无法享受免费退换货服务。请在支付定金前，仔细确认需求，如尺寸，材质，数量。如遇特殊情况，请第一时间和平台沟通，平台会及时响应。</p>
                    </li>
                    <li>
                        <h5>售后及维修</h5>
                        <p>不同品类家具售后服务，时长会有所不同。请在发布需求后，和平台仔细确认，商品清单内会包含售后政策。商品有质量问题或由于个人原因损坏需要维修，可以联系拓者客服申请维修，确认后，即可退回维修。保修期内质量原因导致的商品问题，维修费用由拓者承担。其他情况维修费用客户自行承担。</p>
                    </li>
                    <li>
