<template>
    <div>
        <section class="cashBox">
            <header class="cashTitle clearfix">
                <span class="cashNumber">提现账号</span>
                <el-button type="warning" class="verificationTel" @click="telMaskShow">验证手机，绑定提现账号</el-button>
                <el-button type="warning" class="cashRecordBtn">提现记录</el-button>
            </header>
            <ul class="cashNumberMsg clearfix">
                <li class="cashNumberMsgList">
                    <p>账号</p>
                    <p>17610069474</p>
                    <button class="numberBtn">修改>></button>
                </li>
                 <li class="cashNumberMsgList">
                    <p>账号余额</p>
                    <p>888</p>
                </li>
                 <li class="cashNumberMsgList">
                    <p>已提现</p>
                    <p>1000</p>
                </li>
                 <li class="cashNumberMsgList">
                    <p>累计收益</p>
                    <p>1000</p>
                </li>
            </ul>
            <div class="benefitRecord clearfix">
                <header>收益记录</header>
                <template>
                    <el-tabs :tab-position="tabPosition">
                        <el-tab-pane label="最近一周">
                            <week></week>
                        </el-tab-pane>
                        <el-tab-pane label="最近一个月">
                            <month></month>
                        </el-tab-pane>
                    </el-tabs>
                </template>
                <div class="cashPage">
                    <page></page>
                </div>
            </div>
        </section>
        <div class="telMask" v-if="telMask">
            <ul class="telMaskBox">
                <li class="telMaskList" v-if="telMaskList=='1'">
                    <header class="telMasListTitle">
                        验证手机号
                    </header>
                    <div class="telInput">
                        <div class="telMsg">
                            <input type="text" placeholder="请输入手机号码">
                        </div>
                        <div class="numMsg">
                            <input type="text" placeholder="请输入验证码">
                            <button>获取验证码</button>
                        </div>
                    </div>
                    <p class="TelTips">
                        <i></i>验证码输入错误
                    </p>
                    <div class="telBtn">
                        <button @click="nextZfb">下一步</button>
                    </div>
                    <span class="el-icon-close telClose" @click="telMaskHide"></span>
                </li>
                <li class="telMaskList" v-if="telMaskList=='2'">
                    <header class="telMasListTitle">
                        设置提现账号
                    </header>
                    <div class="telInput zfbInput">
                        <div class="telMsg">
                            <input type="text" placeholder="支付宝真实姓名">
                        </div>
                        <div class="numMsg">
                            <input type="text" placeholder="支付宝账号">
                            
                        </div>
                    </div>
                    <p class="TelTips">
                        <i></i>两次输入不一致<span>提示： 请仔细核对提现账号</span>
                    </p>
                    <div class="telBtn">
                        <button @click="showSuccess">完成</button>
                    </div>
                    <span class="el-icon-close telClose" @click="telMaskHide"></span>
                </li>
                <li class="telMaskList" v-if="telMaskList=='3'">
                    <header class="telMasListTitle">
                        设置提现账号
                    </header>
                    <p class="telSuccess">
                        <img src="../../../img/applySuccess.png" alt="">
                    </p>
                    <p class="telSuccessText">
                       修改成功
                    </p>
                    <div class="cashBtn applyBtn">
                        <button @click="telMaskHide">关闭</button>
                    </div>
                    <span class="el-icon-close telClose" @click="telMaskHide"></span>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
import week from '@/components/userCore/userChild/cashChild/week'
import month from '@/components/userCore/userChild/cashChild/month'
import page from '@/components/page'
export default {
    name : "cash",
    data () {
        return {
            msg : "提现",
            tabPosition: 'top',
            telMask : false,//验证手机号码绑定支付宝弹窗
            telMaskList : "1",//验证手机号显示

        }
    },
    components:{
        week,
        page,
        month
    },
    methods: {
        //弹框的显示隐藏
        telMaskShow () {
            this.telMask = true;
        },
        telMaskHide(){
            this.telMask = false;
            this.telMaskList = "1"
        },
        //显示下一个弹窗
        nextZfb(){
            this.telMaskList = "2"
        },
        //显示验证成功
        showSuccess(){
            this.telMaskList = "3"
        }
        
     }
}
</script>
<style scope>
    .cashBox{
        
    }
    .cashNumber{
        float: left;
        font-size: 16px;
        line-height: 66px;
        color: #333333;
        font-weight: bold;
        margin-left: 39px;
        background: #fff;
    }
    .cashTitle{
        border-bottom: 1px solid #dddddd;
        background: #fff;
    }
    .cashTitle button{
        float: right;
        margin-top: 15px;
        margin-right: 13px;
        
        line-height: 40px;
        font-size: 16px;
        padding: 0;
    }
    .verificationTel{
        width: 192px;
    }
    .cashRecordBtn{
        width: 90px;
    }
    .cashNumberMsg{
        padding: 17px 0 10px 18px;
        background: #fff;
    }
    .cashNumberMsgList{
        float: left;
        width: 216px;
        height: 90px;
        color: #414141;
        text-align: center;
        font-weight: bold;
        border-right: 1px solid #ddd;
    }
    .cashNumberMsgList p:nth-of-type(1){
        font-size: 16px;
        line-height: 40px;
    }
    .cashNumberMsg li:last-of-type{
        border: none;
    }
    .benefitRecord{
        margin-top: 24px;
        background: #fff;
    }
    .benefitRecord header{
        font-size: 16px;
        line-height: 66px;
        font-weight: bold;
        padding-left: 40px;
        border-bottom: 1px solid #dddddd;
    }
    .benefitRecord .el-tabs__header{
        height: 44px!important;
        padding-top: 5px;
        box-sizing: border-box;
    }
    .benefitRecord .el-tabs__header{
        width: 100%;
        padding-left: 32px;
        margin: 0;
    }
    .benefitRecord .el-tabs__item{
        width: 102px;
        text-align: center;
        font-size: 16px;
        line-height: 38px;
        color: #999999;
        font-weight: bold;
        border-left: 2px solid transparent!important;
    }
    .benefitRecord .el-tabs__item:hover{
        border-left: 2px solid transparent;
        background: transparent;
    }
    .benefitRecord .el-tabs__item:hover{
        color: #4b4b4b!important;
        border-left: 2px solid transparent!important;
    }
    .benefitRecord .is-active{
        color: #4b4b4b;
        border-left: 2px solid transparent!important;
    }
    .cashPage{
        padding: 50px 0;
    }
    .cashPage .el-pagination{
        width: 932px;
    }
   .el-pager li{
        background: #f6f6f6;
    }
    .el-pagination .btn-next, .el-pagination .btn-prev{
        background: #f6f6f6;
    }
    .numberBtn{
        font-size: 14px;
        line-height: 24px;
        color: #e3ac46;
        background: transparent;
        border: none;
        padding: 0;
        outline: none;
    }
    .telMask{
        position: fixed;
        top:0;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: 99;
        width: 100%;
        height: 100%;
        background: rgba(0,0,0,.45)
    }
    .telMaskBox{
        width: 386px;
        height:312px;
        background: #fff;
        margin: 300px auto; 
    }
    .telMaskList{
        /* padding-top: 23px; */
        position: relative;
    }
    .telMasListTitle{
        font-size: 18px;
        line-height: 84px;
        color: #333333;
        text-align: center;
        margin-bottom: 5px;
    }
    .telInput input{
        border: none;
        border: 1px solid #ddd;
        height: 38px;
        padding: 0;
        padding-left: 16px;
        font-size: 14px;
        line-height: 38px;
        outline: none;
    }
    .telInput{
        padding-left: 43px;
    }
    .telMsg{
        margin-bottom: 18px;
    }
    .telMsg input{
        width: 298px;
    }
    .numMsg input{
        width: 148px;
    }
    .numMsg button{
        width: 135px;
        height: 40px;
        border: none;
        padding: 0;
        background: #e3aa3f;
        color: #fff;
        margin-left: 7px;
        outline: none;
    }
    .TelTips{
        font-size: 12px;
        line-height: 34px;
        color: #e1706a;
        padding-left: 43px;
    }
    .TelTips i{
        display: inline-block;
        width: 16px;
        height: 16px;
        background: url(../../../img/wonder.png) no-repeat;
        vertical-align: middle;
        margin-right: 6px; 
    }
    .telBtn{
        text-align: center;
        margin-top: 10px;
    }
    .telBtn button{
        width: 300px;
        height: 50px;
        background: #e3aa3f;
        color: #fff;
        padding: 0;
        border: none;
        outline: none;
    }
    .telClose{
        position: absolute;
        top: 15px;
        right: 20px;
        font-size: 20px;
    }
    .zfbInput input{
        width: 298px;
    }
    .TelTips span{
        font-size: 12px;
        color: #bebebe;
        margin-left: 60px;
    }
    .telSuccess{
        margin-top: 8px;
        text-align: center;
        padding: 0;
    }
    .telSuccessText{
        font-size: 18px;
        line-height: 76px;
        text-align: center;
        color: #333333;
    }
</style>



