    height: 86px;
}
.el-textarea textarea{
    height: 86px;
    border: 1px solid #dddddd;
    vertical-align: top;
    background-color: #f3f3f3;
    resize:none;
    margin-left: -6px;
}
.describe{
    vertical-align: top;
}
.el-textarea__inner:focus{
    border-color: #dddddd;
}
.read{
    display: inline-block;
    width: 18px;
    height: 18px;
    background: url(../../img/read.png) no-repeat;
    vertical-align: top;
    margin-right: 22px;
}
.readBox{
    font-size: 14px;
    line-height: 18px;
    color: #484848;
    vertical-align: top;
    padding-left: 58px;
    margin: 17px 0 32px 0;
}
.goUpload{
    padding-left: 120px;
    margin-bottom: 46px;
}
