    width: 186px;
    height: 40px;
    font-size: 18px;
    padding: 0;
}
.listRight{
    float: right;
    width: 432px;
    height: 720px;
    border-left: 1px solid #dddddd;
    padding-left: 54px;
    padding-right: 90px;
    box-sizing: border-box;
}
.listRight li{
    margin-bottom: 19px;
}
.listRight h3{
    font-size: 18px;
    line-height: 50px;
    color: #333333;
    margin-bottom: 10px;
}
.listRight header{
    font-size: 14px!important;
    line-height: 38px!important;
    color: #333333!important;
    padding-left: 0!important;
    border-bottom: none!important;
    font-weight: bold;
}
.listRight p{
    font-size: 12px;
    line-height: 18px;
    color: #767676;
}
.uoloadProgress{
    width: 80px;
    height: 8px;
    border: 1px solid #e3aa3f;
    padding: 1px;
    box-sizing: border-box;
    border-radius: 4px;
    position: absolute;
    top: 40px;
    left: 9px;
    z-index: 9;
}
.uoloadProgressChild{
    position: absolute;
    top: 1px;
    left: 1px;
    border-radius: 2px;
    width: 98%;
    height: 4px;
    background: #e3aa3f;
}
.uoloadProgressBox{
    width: 98px;
    height: 98px;
    border: 1px solid #dddddd;
    background-color: #f3f3f3;
    position: absolute;
    top: -1px;
}
</style>



