<template>
    <div>
        <section class="accountNumber">
            <header class="modifyPassword">
                修改密码
            </header>
            <div class="content">
                <el-form :label-position="labelPosition" label-width="80px" :model="formLabelAlign">
                    <el-form-item label="原密码">
                        <el-input v-model="formLabelAlign.oldPs" placeholder="请输入原密码"></el-input>
                    </el-form-item>
                    <el-form-item label="新密码">
                        <el-input v-model="formLabelAlign.newPs" placeholder="请输入新密码"></el-input>
                    </el-form-item>
                    <el-form-item label="确认密码">
                        <el-input v-model="formLabelAlign.confirmPs" placeholder="请确认新密码"></el-input>
                    </el-form-item>
                </el-form>
                <div class="confirmModify">
                    <el-button type="warning">确认修改</el-button>
                </div>
            </div>
                <footer>
                    提醒：为了您的账号安全，建议定期修改密码。
                </footer>
        </section>
        
    </div>
</template>
<script>
export default {
    name : "accountNumber",
    data () {
        return {
            msg : "账号",
            labelPosition: 'left',
            formLabelAlign: {
                oldPs: '',
                newPs: '',
                confirmPs: ''
            }
        }
    }
}
</script>
<style>
.accountNumber{
    height: 730px;
    background: #fff;
}
.modifyPassword{
    font-size: 16px;
    line-height: 60px;
    font-weight: bold;
    border-bottom: 1px solid #dddddd;
    padding-left: 40px;
}
.content{
    width: 412px;
    padding-top: 26px;
    margin-left: 40px;
}
.el-input.is-active .el-input__inner, .el-input__inner:focus{
    border-color: #e3aa3f;
}
.confirmModify{
    padding-left: 80px;
}
.el-button--warning{
    width: 186px;
    height: 40px;
    border: none;
    border-radius: 0;
}
.accountNumber footer{
    font-size: 12px;
    line-height: 60px;
    color: #9b9b9b;
    padding-left: 112px;
    border-top: 1px solid #dddddd;
    margin-top: 361px;
}
</style>


