<template>
    <div>
        <section class="wrap">
            <header class="authorHead clearfix">
                <div class="authorHeadLeft">
                    <div class="authorTitle">
                        <img src="../../img/authorTitle.png" alt="">
                    </div>
                    <div class="authorInfo">
                        <p>阿飞的小蝴蝶</p>
                        <p>为你带来最新的最好的模型</p>
                    </div>
                </div>
                <div class="authorHeadRight">
                    <p>120</p>
                    <p>模型</p>
                </div>
            </header>
            <modeList></modeList>
            <page></page>
        </section>
    </div>
</template>
<script>
    import modeList from "@/components/modeList"
    import page from "@/components/page"
    export default{
        name : "author",
        data () {
            return {
                msg : "我是作者"
            }
        },
        components : {
            modeList,
            page
        },
        methods:{
            
        }
        
    }
</script>
<style>
.authorHead{
    height: 152px;
    margin: 22px 0;
    background-color: #fff;
}
.authorHeadLeft{
    float: left;
    width: 350px;
}
.authorHeadRight{
    float: right;
}
.authorTitle{
    float: left;
    
    height: 80px;
    margin: 37px 0 0 37px;
}
.authorTitle img{
    width: 80px;
    height: 80px;
    border-radius: 40px;
}
.authorInfo{
    float: left;

}
.authorInfo p:nth-of-type(1){
    font-size: 18px;
    line-height: 34px;
    color: #333333;
    margin-top: 48px;
    margin-left: 20px;
    font-weight: 200;
}
.authorInfo p:nth-of-type(2){
    font-size: 14px;
    line-height: 30px;
    color: #676767;
    margin-left: 14px;
}
.authorHeadRight p:nth-of-type(1){
    font-size: 36px;
    line-height: 36px;
    font-weight: bold;
}
.authorHeadRight p:nth-of-type(1){
    font-size: 18px;
    line-height: 30px;
    color: #333333;
    margin: 43px 34px 0 0;
}
</style>



