<template>
    <div>
        <section class="subject">
            <div class="wrap bannerBox">
                <img src="../../img/vipBanner.png" alt="">
            </div>
            <div class="wrap">
                <header class="vipTitle">
                    <p>VIP免费模型</p>
                    <p>每周更新<span></span></p>
                </header>
                <modeList></modeList>
            </div>
            <page></page>
            
        </section>
        
    </div>    
</template>
<script>
import modeList from '@/components/modeList'
import Page from '@/components/page'
    export default{
        name : "vip",
        data(){
            return {
                msg : "我是vip页面"
            }
        },
        components:{
            Page,
            modeList
        }
    }
</script>
<style scope>
@import "../../css/vip.css";
@import "../../css/global";
.bannerBox{
    height: 292px;
    margin-top: 14px;
    margin-bottom: 22px;
}
.bannerBox img{
    display: block;
}
.vipTitle{
    text-align: center;
    margin-bottom: 30px;
}

.vipTitle p:nth-of-type(1){
    font-size: 20px;
    line-height: 34px;
    color: #333333;
}
.vipTitle p:nth-of-type(2) {
  font-size: 16px;
  line-height: 26px;
  color: #707070;
  position: relative;
}
.vipTitle p:nth-of-type(2) span{
    position: absolute;
    width: 47px;
    height: 3px;
    background: #e3aa3f;
    bottom: -1px;
    left: 560px;

}


</style>



