<template>
  <div class="wrap pageBox">
        <el-pagination
        :page-size="30"
        :pager-count="11"
        layout="prev, pager, next"
        :total="300"
        @current-change="pageShow"
        >
        </el-pagination>
    </div>
</template>

<script>

export default {
    name : "page",
    data () {
        return {
        current:1,
        total:10
        }
    },
    methods : {
        pageShow(value){
            console.log(value)
        }
    }
}
</script>
<style>
.pageBox{
    text-align: center;
    margin-bottom: 42px;
}
.el-pager li{
    width: 38px!important;
    height: 38px!important;
    line-height: 38px!important;
    margin: 0 4px!important;
    color: #363636!important;
    border-radius: 3px!important;
}
.el-pagination .btn-next, .el-pagination .btn-prev{
    height: 38px;
    margin: 0 4px;
    color: #a0a6a2!important;
     border-radius: 3px;
}
.el-pager .more::before{
    line-height: 38px;
}
.el-pager li.active{
    background: #e3aa3f;
    color: #fff!important;
}
.el-pager li:hover{
    background: #e3aa3f;
    color: #fff!important;

}
</style>



