<template>
    <div>
         <section class="wrap aboutMe">
            <template>
            <el-tabs :tab-position="tabPosition">
                <el-tab-pane label="消息">
                    <userHead></userHead>
                    <news></news>
                </el-tab-pane>
                <el-tab-pane label="账号设置">
                    <userHead></userHead>
                    <accountNumber></accountNumber>
                </el-tab-pane>
                <el-tab-pane label="个人资料">
                    <userHead></userHead>
                    <userMsg></userMsg>
                </el-tab-pane>
                <el-tab-pane label="收藏夹">
                    <userHead></userHead>
                    <collection></collection>
                </el-tab-pane>
                <el-tab-pane label="上传记录">
                    <userHead></userHead>
                    <uploadRecord></uploadRecord>
                </el-tab-pane>
                <el-tab-pane label="下载记录">
                    <userHead></userHead>
                    <download></download>
                </el-tab-pane>
                <el-tab-pane label="提现">
                    <userHead></userHead>
                    <cash></cash>
                </el-tab-pane>
            </el-tabs>
            </template>
        </section>
    </div>
</template>
<script>
import news from '@/components/userCore/userChild/news'//消息
import accountNumber from '@/components/userCore/userChild/accountNumber'//账号设置
import userHead from '@/components/userCore/userChild/userHead'//统一的头部
import userMsg from '@/components/userCore/userChild/userMsg'//个人资料
import collection from '@/components/userCore/userChild/collection'//收藏
import uploadRecord from '@/components/userCore/userChild/uploadRecord'//上传
import download from '@/components/userCore/userChild/download'//下载
import cash from '@/components/userCore/userChild/cash'//下载
export default {
    name : "userCore",
    data () {
        return {
            msg : "jdslajfdlas",
            tabPosition: 'left'
        }
    },
    components: {
        news,
        accountNumber,
        userHead,
        userMsg,
        collection,
        uploadRecord,
        download,
        cash
    }
}
</script>
<style>
    .aboutMe{
        margin-top: 15px;
        margin-bottom: 33px;
    }
    .el-tabs__header{
        width: 223px;
        height: 330px!important;
        background: #fff!important;
        text-align: left!important;
        padding-top: 4px;
    }
    .el-tabs--left .el-tabs__item.is-left{
        text-align: left!important;
        border-left: 2px solid #fff!important;
    }
    .el-tabs--left .el-tabs__item:hover{
        color: #fff!important;
        border-left: 2px solid #e1a638!important;
        background: #e1a638!important;
    }
    .is-active{
        border-left: 2px solid #e1a638!important;
    }
    .el-tabs__item{
        line-height: 40px;
        height: 46px;
        padding: 0 25px;
    }
    .el-tabs__item.is-active{
        color: #e1a638!important;
    }
    .el-tabs__nav .el-tabs__item.is-active{
       
        border-left: 2px solid #e1a638!important;
    }
    .el-tabs--left .el-tabs__active-bar.is-left, .el-tabs--left .el-tabs__nav-wrap.is-left::after{
        left:0;
        
    }
    .el-tabs__nav-wrap::after{
        background: transparent;
    }
    .el-tabs__active-bar{
        display: none;
    }
    .el-tabs--left .el-tabs__header.is-left{
        margin-right: 15px;
    }
</style>


