<script>
export default {
  name: 'App',
  data(){
    return {
      data : {
          accountNumber : false,//我的账号开关
          modeSort : false,//模型开关
        },
        loginName: 'login',//注册登录选项卡
        loginShow : false,//注册登录弹出框
    }
  },
  methods : {
     anShow(){//我的账号鼠标移入事件
          this.data.accountNumber = true;
      },
      anHide(){//我的账号鼠标移出事件
          this.data.accountNumber = false;
      },
      modeListShow(){//模型鼠标移入事件
          this.data.modeSort = true;
      },
      modeListHide(){//模型鼠标移出事件
          this.data.modeSort = false;
      }, 
      goTop(){//返回顶部
        document.body.scrollTop = 0
        document.documentElement.scrollTop = 0
      },
      handleClick(tab, event) {//注册登录选项卡
        console.log(tab, event);
      },
      loginMaxBox(){//登录弹出框
        this.loginShow = true;
      },
      loginMaskClose(){
          this.loginShow = false;
      }
  },
    watch:{
        $route(to,from){
            var navs = document.getElementsByClassName("navInfo")[0];
            var navsDiv = navs.getElementsByTagName("div");
            for(var i=0;i<navsDiv.length;i++){
                navsDiv[i].style.background = "";
            }
            console.log(to)
            if(to.path=="/"){
                document.getElementsByClassName("modeSortBox")[0].style.background = "#e3aa3f";
            }else if(to.path=="/uploadMode"){
                document.getElementsByClassName("modeSortBox")[0].style.background = "#e3aa3f";
            }else{
                if(!document.getElementsByClassName(to.name)[0]){
                    document.getElementsByClassName("modeSortBox")[0].style.background = "#e3aa3f";
                }else{
                    document.getElementsByClassName(to.name)[0].style.background = "#e3aa3f";
                }
                
            }

        }
    },
  mounted() {
      document.getElementsByClassName("modeSortBox")[0].style.background = "#e3aa3f";
        
  },
}
</script>

