    display: inline-block;
}
.registerImg button{
    width: 24px;
    height: 20px;
    background: url(./img/refresh.png) no-repeat;
    padding: 0;
    border: none;
    vertical-align: top;
    margin-top: 8px;
    margin-left: 11px;
    outline: none;
    cursor: pointer;
}
.inputTel span{
    font-size: 12px;
    line-height: 16px;
    color: #717171;
}
.inputTel span:nth-of-type(1){
    float: left;
    width: 100px;
    padding-left: 22px;
    box-sizing: border-box;
    background: url(./img/inputTel.png) no-repeat 0 1px;
}
.inputTel span:nth-of-type(2){
    float: right;
}
.loginBtn{
    margin-top: 34px;
}
.loginBtn button{
    width: 100%;
    height: 50px;
    background: #e3aa3f;
    border: none;
    font-size: 16px;
    line-height: 50px;
    color: #fff;
    outline: none;
}
.registerBox{
    height: 430px;
    padding: 0 43px;
    background-color: #fff;
}
.registerBox li{
    margin-bottom: 17px;
}
.registerBox input{
    width: 298px;
    height: 38px;
    border: none;
    padding: 0;
    outline: none;
    border: 1px solid #ddd;
    font-size: 14px;
    line-height: 38px;
    padding-left: 50px;
    box-sizing: border-box;
}
.registerBox li:nth-of-type(1) input{
    background: url(./img/nameImg.png) no-repeat 10px 7px;
}
.registerBox li:nth-of-type(2) input{
    background: url(./img/zcTelImg.png) no-repeat 10px 7px;
}
.registerBox li:nth-of-type(4) input,.registerBox li:nth-of-type(5) input{
    background: url(./img/zcPwImg.png) no-repeat 10px 7px;
}
.registerBox li:nth-of-type(3) input{
    width: 148px;
    padding-left: 10px;
}
.registerBox li:nth-of-type(5){
    margin-bottom: 0;
}
.registerBox li:nth-of-type(3) button{
    width: 135px;
    height: 40px;
    font-size: 14px;
    line-height: 40px;
    color: #fff;
    border: none;
    padding: 0;
    outline: none;
    background: #e3aa3f;
    margin-left: 9px;
}
.pwProblem{
    font-size: 12px;
    line-height: 30px;
    color: #e78984;
    padding-left: 32px;
    background: url(./img/wonder.png) no-repeat 7px 7px;
    margin-bottom: 14px;
}
.registerBtn div button{
    width: 300px;
    height: 50px;
    background: #e3aa3f;
    border: none;
    outline: none;
    color: #fff;
    font-size: 16px;
    line-height: 50px;
}
.registerBtn p{
    font-size: 12px;
    line-height: 50px;
    color: #6c6c6c;
    padding-left: 32px;
}
.registerBtn p span{
    color: #e6b252;
    margin-left: -6px;
}
</style>



