<template>
    <div>
        <header class="clearfix updataTitle">
               <span>最近更新</span>
               <span>查看更多>></span>
           </header>
            <swiper :options="swiperOption" ref="Swiper1" class="swiper-no-swiping">
                <swiper-slide v-for="(value,key) in updataImg" :key="key" class="uploadImgBox"><img class="uploadImgItem" :src="value" alt=""></swiper-slide>
            </swiper>
            <span class="swiper-button-prev prev" slot="button-prev"></span>
            <span class="swiper-button-next next" slot="button-next"></span>
    </div>
</template>
<script>
// import { swiper, swiperSlide } from 'vue-awesome-swiper';
var updataImg = [require("../img/update.png"),require("../img/infoImg.png"),require("../img/update.png"),require("../img/infoImg.png"),require("../img/infoImg.png"),require("../img/update.png")];
    export default{
        name : "updates",
        data(){
            return {
                msg : "我是最近更新",
                updataImg : updataImg,
                swiperOption: {//以下配置不懂的，可以去swiper官网看api，链接http://www.swiper.com.cn/api/
                    // notNextTick是一个组件自有属性，如果notNextTick设置为true，组件则不会通过NextTick来实例化swiper，也就意味着你可以在第一时间获取到swiper对象，<br>　　　　　　　　假如你需要刚加载遍使用获取swiper对象来做什么事，那么这个属性一定要是true
                    notNextTick: true,
                    // swiper configs 所有的配置同swiper官方api配置
                    direction : 'horizontal',
                    prevButton:'.swiper-button-prev',//上一张
                    nextButton:'.swiper-button-next',//下一张
                    observeParents:true,
                    // 如果自行设计了插件，那么插件的一些配置相关参数，也应该出现在这个对象中，如下debugger
                    debugger: true,
                    slidesPerView : 4,
                    slidesPerGroup : 1,
                    //   pagination: {
                    //         el: '.swiper-pagination',
                    //         clickable: true,
                    //     },
                        navigation: {
                            nextEl: '.swiper-button-next',
                            prevEl: '.swiper-button-prev',
                        },
                    width: 1189,
                    //   slidesPerView : 3
                    }
            }
        }
    }
</script>
<style scope>
.soonUpdata{
  position: relative;
}

.soonUpdata .prev{
  position: absolute;
  top: 135px;
  left: 0;
  width: 48px;
  height: 50px;
  background: url(../img/prevactive.png) no-repeat;
  outline: none;

}
.soonUpdata .prev:hover{
  background: url(../img/prev.png) no-repeat;


}
.soonUpdata .next{
  position: absolute;
  top: 135px;
  right: -1px;
  width: 48px;
  height: 50px;
  background: url(../img/nextactive.png) no-repeat;
  outline: none;

}
.soonUpdata .next:hover{
  background: url(../img/next.png) no-repeat;

}
.updataTitle span:nth-of-type(1){
    float: left;
    font-size: 20px;
    line-height: 54px;
    color: #363636;
    padding-left: 2px;
    font-weight: bold;
}
.updataTitle span:nth-of-type(2) {
    float: right;
    font-size: 14px;
    line-height: 40px;
    color: #999999;
    margin-top: 14px;
    cursor: pointer;
}
</style>




