import Vue from 'vue'
import Router from 'vue-router'
import Index from "@/components/index/index";
import mode3D from "@/components/mode3D/mode";
import Vip from "@/components/vipSpecial/vip";
import ModeInfo from "@/components/modeInfo/modeInfo";
import Author from "@/components/author/author";
import uploadMode from "@/components/uploadMode/uploadMode";
import aboutTz from "@/components/about/aboutTz";
import userCore from "@/components/userCore/userCore";
import Recharge from "@/components/recharge/recharge";

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: "/",
      name: "Index",
      component: Index
    },
    {
      path: "/mode3D",
      name: "mode3D",
      component: mode3D
    },
    {
      path: "/vip",
      name: "vip",
      component: Vip
    },
    {
      path: "/modeInfo",
      name: "modeInfo",
      component: ModeInfo
    },
    {
      path: "/author",
      name: "author",
      component: Author
    },
    {
      path: "/uploadMode",
      name: "uploadMode",
      component: uploadMode
    },
    {
      path: "/aboutTz",
      name: "aboutTz",
      component: aboutTz
    },
    {
      path: "/userCore",
      name: "userCore",
      component: userCore
    },
    {
      path: "/Recharge",
      name: "Recharge",
      component: Recharge
    }
  ]
});



