<template>
    <div>
        <div>
            <swiper :options="swiperOption" ref="mySwiper" class="bannerOption swiper-no-swiping">
                <swiper-slide v-for="(item,index) in bannerArr" :key="index" class="uploadImgBox"><img :src="item" alt=""></swiper-slide>
            </swiper>
        </div>
        <div class="bottomIcon swiper-pagination" slot="pagination">
        </div>
    </div>
</template>
<script>
    var bannerArr = [require('../img/a.jpg'),require('../img/b.jpg'),require('../img/c.jpg')];
    import { swiper, swiperSlide } from 'vue-awesome-swiper';
    export default{
        name : "banner",
        data () {
            return {
                "bannerArr" : bannerArr,
                swiperOption: {
                    pagination: {
                            el: '.swiper-pagination',
                            clickable: true
                    },
                    autoplay: {
                        autoplay: 2000,
                        spaceBetween: 50,
                        disableOnInteraction: false,
                    },
                    loop: true,
                    lockSwipes: true,
                    
                }
            }
        },
        mounted () {
            
        }
    }
</script>
<style scope>
.banner{
    height: 508px;
    position: relative;
    margin-bottom: 17px;
}
.banner img{
    width: 100%;
    height: 508px;

}
.bottomIcon{
    position: absolute;
    bottom: 10px;
    left: 0;
    width: 100%;
    height: 24px;
    text-align: center;
}

.bottomIcon span{
    display: inline-block;
    width: 30px;
    height: 5px!important;
    border-radius: 3px;
    margin: 0 7px;
    outline: none;
}
.swiper-pagination-bullet{
    opacity: 1!important;
    background: #fff!important;
}
.swiper-pagination-bullet-active{
    background: #d9ad40!important;
}
.swiper-slide{
    padding-top: 0;
}
</style>




